import { useState } from 'react';
import {
  FaBriefcase,
  FaCode,
  FaPhone,
  FaReact,
  FaRegAddressCard,
  FaUniversity
} from 'react-icons/fa';

import NavTab from './NavTab';
import './NavBar.css';

const NavBar = () => {
  // Sets the root page as the default activeTab
  const [ activeTab, setActiveTab ] = useState(process.env.PUBLIC_URL + '/');
  let path = activeTab
  console.log('activeTab:', activeTab);
  console.log('process.env.PUBLIC_URL:', process.env.PUBLIC_URL);
  console.log('path:', path);
  console.log('process.env.PUBLIC_URL + path:', process.env.PUBLIC_URL + path);
  console.log('activeTab === process.env.PUBLIC_URL + path:', activeTab === process.env.PUBLIC_URL + path);

  const tabs = [
    { label: 'Profile', path: '/', icon: <FaRegAddressCard /> },
    { label: 'Projects', path: '/projects', icon: <FaCode /> },
    { label: 'Experience', path: '/experience', icon: <FaBriefcase /> },
    { label: 'Education', path: '/education', icon: <FaUniversity /> },
    { label: 'Contact', path: '/contact', icon: <FaPhone /> },
  ];

  const renderTab = (tab) => {
    return (
      <NavTab
        key={ tab.label }
        label={ tab.label }
        path={ tab.path }
        icon={ tab.icon }
        activeTab={ activeTab }
        setActiveTab={ setActiveTab }
      />
    );
  };

  return (
    <nav>
      { tabs.map(renderTab) }
      <div className='divider web' />
      <p className='info web'>I built this site with <FaReact /> React</p>
    </nav>
  );
};

export default NavBar;
